.projects-container {
  text-align: end;
  padding-right: 3rem;
}

.resume {
  margin-top: 1rem;
  padding-bottom: 0.2rem;

  text-align: end;
  cursor: pointer;
}


.projects-title {
  
}


.divider {
  min-height: 100%;

  border-left: 0.2rem solid var(--accent);
  border-radius: 10px;
}

.project-list {
  /* padding-top: 1rem; */

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: 1.25rem;
}







.sub-title-container {
  position: relative;
  width: fit-content;
  margin-left: auto;
  overflow: hidden;

  margin-top: 0.25rem;
  margin-bottom: 0.25rem;

  border-radius: 5px;
  cursor: pointer;

  /* background-color: rebeccapurple; */
}

.sub-title {
  position: relative;

  padding-top: 0.65rem;
  padding-bottom: 0.65rem;
  padding-left: 1rem;
  padding-right: 1rem;

  color: var(--accent);
  
  transition: color 300ms ease;
  /* z-index: 2; */
  mix-blend-mode: difference;
}

.sub-title-bg {
  /* z-index: 1; */
  position: absolute;
  
  width: 130%;
  height: 380%;

  margin-top: -27%;
  margin-left: -5%;
  
  /* background: linear-gradient(0deg, #FFF, #FFF); */
  background: linear-gradient(0deg, var(--accent), var(--accent));
  background-size: 200%;
  background-repeat: no-repeat;
  background-position: 200% 0;
  
  transform: rotate(-25deg);
  /* mix-blend-mode: difference; */

  /* transition: background-position 400ms cubic-bezier(0.900, 0.005, 0.180, 1.000); */
  transition: background-position 300ms cubic-bezier(0.680, 0.125, 0.360, 0.830);
  /* transition: background-position 400ms ease; */
}

.sub-title-container:hover .sub-title-bg {
  background-position: 100% 0;
}












