.title-container {
  position: relative;
  width: fit-content;
  margin-left: auto;
  overflow: hidden;
  
  margin-top: 0.25rem;
  margin-bottom: 0.25rem;
  
  margin-right: -2rem;

  /* border-radius: 150px; */
  cursor: pointer;
  
  /* z-index: 4; */
  /* background-color: rebeccapurple; */
}

.title {
  position: relative;
  align-self: center;
  
  margin-top: -1rem;
  padding-bottom: 1.15rem;
  
  padding-left: 3rem;
  padding-right: 3rem;  
  /* z-index: 2; */
  color: white;
}

.title-bg {
  /* z-index: 2; */
  position: absolute;
  
  width: 80%;
  height: 250%;

  margin-top: -21%;
  margin-left: 10%;
  
  /* background: linear-gradient(0deg, #FFF, #FFF); */
  background: linear-gradient(120deg, #1a2a6c, #1a2a6c, #1a2a6c, #b21f1f, #fdbb2d);
  /* background: linear-gradient(0deg, var(--accent), var(--accent)); */
  background-size: 200%;
  background-repeat: no-repeat;
  background-position: 200% 0;
  
  transform: rotate(-20deg);
  /* mix-blend-mode: difference; */

  /* transition: background-position 400ms cubic-bezier(0.900, 0.005, 0.180, 1.000); */
  transition: background-position 200ms cubic-bezier(0.680, 0.125, 0.360, 0.830);
  /* transition: background-position 400ms ease; */
}

.title-container:hover .title-bg {
  background-position: 100% 0;
}
