


.projects-section {
  width: 100vw;
  height: 370vh;

  overflow-y: hidden;

  background-color: var(--bg-primary);
  /* background-color: rgb(10, 5, 27); */

  padding-top: 140rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}


.projects-title {
  /* padding-bottom: 5rem; */
  padding-right: 7rem;
  margin-left: auto;

  color: var(--accent);
  /* color: rgb(176, 240, 78); */
  font-family: 'Flexible H';
  font-size: 13rem;
  letter-spacing: 0.2rem;
}


.project-container {
  width: fit-content;
  height: 40rem;

  display: flex;
  flex-direction: column;

  margin-left: 3vh;
  margin-right: 3vh;
  margin-top: 3rem;
  margin-bottom: 3rem;
  /* margin-left: 35vw;
  margin-right: 35vw; */
  /* background-color: rgb(37, 37, 48); */
}

.project-container-left {
  margin-right: 35vw;
}
.project-container-right {
  margin-left: 35vw;
}

/* @media (min-aspect-ratio: 15/9) {
  .project-container {
    margin-left: 25vw;
    margin-right: 25vw;
  }
} */




.project-heading {
  display: flex;
}

.project-title-container {
  display: flex;
  flex-direction: column;
}

.project-number {
  color: rgb(176, 240, 78);

  font-family: 'AppleTea';
  font-size: 9rem;
}

.project-title {
  color: var(--accent);
  /* color: rgb(176, 240, 78); */

  font-family: 'AppleTea';
  font-size: 4rem;
}


.project-content {
  margin-top: -1.5rem;

  display: flex;
  /* align-items: center; */

  color: var(--accent);
  color: hsl(49, 64%, 93%);
  /* color: rgb(255, 245, 240); */
  font-family: "Plus_Jakarta_Sans";
  letter-spacing: 0.015rem;
  word-spacing: 0.06rem;
  /* font-family: 'Merriweather'; */
  font-weight: 100;
}

.project-info {
  display: flex;
  flex-direction: column;
  
  margin-top: 1rem;

  max-width: 50rem;
}

.project-tags {
  display: flex;
  flex-direction: row;

  padding-bottom: 1rem;
}

.project-description {
  font-size: 1.15rem;
}

.project-key-points {
  padding-top: 1rem;
}

.project-key-points div {
  padding-right: 1.5rem;
  padding-left: 1rem;
  padding-top: 1.5rem;
  font-size: 1.1rem;
}





.project-preview {
  /* width: 45rem;
  height: 25rem; */
  width: fit-content;
  height: fit-content;
  /* max-width: 45rem;
  max-height: 27rem; */


  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  margin-left: 3rem;
  margin-right: 3rem;
  margin-top: -1rem;

  color: white;
  text-decoration: none;

  background-color: rgba(71, 71, 71, 0.368);
  background-size: cover;
  background-repeat: no-repeat;

  
  /* box-shadow: 0px 0px 0px 0px var(--bg-primary) inset; */
  box-shadow: 0px 0px 250px 10px rgba(0, 0, 0, 0.0) inset;
  
  transition: filter 300ms ease, box-shadow 300ms ease;
}

/* .project-preview:hover {
  box-shadow: 0px 0px 250px 25px rgba(0, 0, 0, 0.7) inset;
} */

.project-preview-shadows {
  position: absolute;
  width: 100%;
  height: 100%;
  
  box-shadow: 0px 0px 250px 10px rgba(0, 0, 0, 0.0) inset;
  transition: box-shadow 300ms ease;
}

.project-preview:hover .project-preview-shadows {
  box-shadow: 0px 0px 250px 25px rgba(0, 0, 0, 0.85) inset;
}


.project-preview-img {
  /* width: 100%;
  height: 100%; */
  max-width: 50rem;
  max-height: 30rem;

  border-radius: 2px;
}


.project-preview-text {
  position: absolute;

  padding: 0.55rem;
  padding-left: 1.75rem;
  padding-right: 1.75rem;
  
  letter-spacing: 0.075rem;
  background-color: rgba(36, 36, 36, 0.8);
  border-radius: 30px;
  
  box-shadow: 0 0 40px 2px rgba(2, 4, 6, 0.2);

  filter: opacity(0.0);
  
  transition: filter 200ms ease, background-color 200ms ease;
}


.project-preview:hover .project-preview-text {
  color: white;
  filter: opacity(1.0);
}

.project-preview:hover .project-preview-text:hover {
  filter: opacity(1.0);
  background-color: rgba(30, 33, 36, 0.95);
}



