.nbody-root-container {
  // --bg-primary: rgb(12, 13, 15);
  // --bg-complementary: rgb(223, 218, 208);
  --shadow: rgb(27, 27, 27, 0.87);
  --accent: hsl(49, 64%, 90%);

  width: 100vw;
  height: 100vh;
  background-color: black;

  display: flex;
  flex-direction: column;
  align-items: stretch;

  z-index: 0;

  .home-button-container {
    position: absolute;
    top: 0;
    left: 0.5rem;

    cursor: pointer;
    user-select: none;

    // color: white;
    font-family: 'BigJhon';
    
    font-size: 1.6rem;
    font-weight: 900;
    
    z-index: 10;
    width: 100px;
    height: 150px;
    z-index: 9;
    
    @keyframes gradient-animation {
      0% { --test: 0deg; }
      1% { --test: 3.6deg; }
      2% { --test: 7.2deg; }
      3% { --test: 10.8deg; }
      4% { --test: 14.4deg; }
      5% { --test: 18deg; }
      6% { --test: 21.6deg; }
      7% { --test: 25.2deg; }
      8% { --test: 28.8deg; }
      9% { --test: 32.4deg; }
      10% { --test: 36deg; }
      11% { --test: 39.6deg; }
      12% { --test: 43.2deg; }
      13% { --test: 46.8deg; }
      14% { --test: 50.4deg; }
      15% { --test: 54deg; }
      16% { --test: 57.6deg; }
      17% { --test: 61.2deg; }
      18% { --test: 64.8deg; }
      19% { --test: 68.4deg; }
      20% { --test: 72deg; }
      21% { --test: 75.6deg; }
      22% { --test: 79.2deg; }
      23% { --test: 82.8deg; }
      24% { --test: 86.4deg; }
      25% { --test: 90deg; }
      26% { --test: 93.6deg; }
      27% { --test: 97.2deg; }
      28% { --test: 100.8deg; }
      29% { --test: 104.4deg; }
      30% { --test: 108deg; }
      31% { --test: 111.6deg; }
      32% { --test: 115.2deg; }
      33% { --test: 118.8deg; }
      34% { --test: 122.4deg; }
      35% { --test: 126deg; }
      36% { --test: 129.6deg; }
      37% { --test: 133.2deg; }
      38% { --test: 136.8deg; }
      39% { --test: 140.4deg; }
      40% { --test: 144deg; }
      41% { --test: 147.6deg; }
      42% { --test: 151.2deg; }
      43% { --test: 154.8deg; }
      44% { --test: 158.4deg; }
      45% { --test: 162deg; }
      46% { --test: 165.6deg; }
      47% { --test: 169.2deg; }
      48% { --test: 172.8deg; }
      49% { --test: 176.4deg; }
      50% { --test: 180deg; }
      51% { --test: 183.6deg; }
      52% { --test: 187.2deg; }
      53% { --test: 190.8deg; }
      54% { --test: 194.4deg; }
      55% { --test: 198deg; }
      56% { --test: 201.6deg; }
      57% { --test: 205.2deg; }
      58% { --test: 208.8deg; }
      59% { --test: 212.4deg; }
      60% { --test: 216deg; }
      61% { --test: 219.6deg; }
      62% { --test: 223.2deg; }
      63% { --test: 226.8deg; }
      64% { --test: 230.4deg; }
      65% { --test: 234deg; }
      66% { --test: 237.6deg; }
      67% { --test: 241.2deg; }
      68% { --test: 244.8deg; }
      69% { --test: 248.4deg; }
      70% { --test: 252deg; }
      71% { --test: 255.6deg; }
      72% { --test: 259.2deg; }
      73% { --test: 262.8deg; }
      74% { --test: 266.4deg; }
      75% { --test: 270deg; }
      76% { --test: 273.6deg; }
      77% { --test: 277.2deg; }
      78% { --test: 280.8deg; }
      79% { --test: 284.4deg; }
      80% { --test: 288deg; }
      81% { --test: 291.6deg; }
      82% { --test: 295.2deg; }
      83% { --test: 298.8deg; }
      84% { --test: 302.4deg; }
      85% { --test: 306deg; }
      86% { --test: 309.6deg; }
      87% { --test: 313.2deg; }
      88% { --test: 316.8deg; }
      89% { --test: 320.4deg; }
      90% { --test: 324deg; }
      91% { --test: 327.6deg; }
      92% { --test: 331.2deg; }
      93% { --test: 334.8deg; }
      94% { --test: 338.4deg; }
      95% { --test: 342deg; }
      96% { --test: 345.6deg; }
      97% { --test: 349.2deg; }
      98% { --test: 352.8deg; }
      99% { --test: 355deg; }
      100% { --test: 360deg; }
    }

    span {
      background: linear-gradient(var(--test), #006aff 30%, #ffaa00 70%);
      background: -webkit-linear-gradient(var(--test), #006aff 30%, #ffaa00 70%);
      // background: -webkit-linear-gradient(-60deg, hwb(203 0% 0%) 30%, hwb(87 0% 0%) 70%);
      // background: -webkit-linear-gradient(-65deg, hwb(246 0% 0%) 20%, hwb(24 0% 0%) 70%);
      // background: -webkit-linear-gradient(45deg, hwb(0 0% 0%), hwb(229 0% 0%) 80%);
      animation: gradient-animation 10s infinite;
      // filter: blur(1.0px);
      background-size: 100px 100px;
      // background-size: 100px ;
      background-position: 0px 14px;
      background-repeat: no-repeat;
      
      -webkit-background-clip: text;
      color: white;
      text-shadow: 1px 1px 5px black;
      // color: transparent;

      transition: color 500ms ease, text-shadow 1000ms ease;
      // background-color: rgb(255, 0, 0);
      // color: white;
      // background-blend-mode: color-burn;
      // -webkit-text-fill-color: transparent;
    }


    .D {
      position: absolute;
      font-size: 6rem;
      font-weight: 400;
    }
    
    .E {
      position: absolute;
      margin-top: 1.8rem;
      margin-left: 1.15rem;      
      background-position: -1rem -1rem;
    }
    
    .N {
      position: absolute;
      margin-top: 1.8rem;
      margin-left: 2.2rem;
      background-position: -2.1rem -1rem;
    }
    
    .Y {
      position: absolute;
      margin-top: 3.3rem;
      margin-left: 1.25rem;
      background-position: -1rem -2.5rem;
    }
    
    .S {
      position: absolute;
      margin-top: 3.3rem;
      margin-left: 2.4rem;
      background-position: -2.1rem -2.5rem;
    }

  }

  .home-button-container:hover span {
    color: transparent;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0);
    transition: color 500ms ease, text-shadow 100ms ease;
  }

  #video {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    
    @media (max-aspect-ratio: 16/9) {
      top: 0;
    }
    
    // width: min(100vw, 180vh);
    // height: min(56.25vw, 100vh);
    width: min(100vw, 215vh);
    height: min(56.25vw, 120vh);
    
    // margin-top: auto;
    // margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    
    z-index: 1;
  }
  
  .project-title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    cursor: default;
    user-select: none;
    pointer-events: none;

    z-index: 2;
  }
  
  .project-title {  
    color: aliceblue;
    
    font-size: 7rem;
    font-family: 'Rocketwildness';
    font-weight: 900;
    text-align: center;
  
    z-index: 1;

    text-shadow: 1px 1px 5px black;
  }




  .load-screen {
    position: absolute;
    width: 100vw;
    height: 100vh;
    
    z-index: 11;
    pointer-events: none;

    background-color: transparent;
    transition: background-color 500ms ease;
  }
  
  .load-screen-visible {
    background-color: rgb(1, 2, 9);
  }
  


}

