.my-photo {
  width: 30vw;
  height: min(60vw, 100vh);

  background-repeat: no-repeat;

  /* background-position: -18.5vw -17vw;
  background-size: 60vw;
  background-image: url('assets/me-v6.png'); */

  background-position: 0vw -2vw;
  background-size: 32vw;
  background-image: url('assets/me-v6.1.png');

  filter: saturate(1.0) brightness(1.0);
  transition: filter 300ms ease;
}

.name-title {
  /* rgb(255, 245, 240) */
  color: var(--accent); 
  font-family: 'Flexible H';
  font-size: 25rem;
  letter-spacing: .75rem;
  
  margin-top: 0vh;
  margin-left: 5vw;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-items: flex-start;
  text-shadow: 1px 1px rgb(241, 109, 109), -3px -3px rgb(144, 144, 255);
  /* text-shadow: 2px 2px rgb(238, 52, 52), -3px -3px rgb(35, 35, 241); */
  /* text-shadow: 3px 3px rgb(239, 82, 82), -5px -5px rgb(84, 84, 252); */
  filter: opacity(1);
  transition: filter 500ms ease;
  /* transition: all 1500ms cubic-bezier(0.000, 0.895, 0.910, 0.755); */
}


.name-title-fade-out {
  filter: opacity(0);
}


.last-name {
  align-self: flex-end;
  margin-top: -5rem;
  /* margin-left: 13.5rem; */
  margin-left: 23rem;
  
}

.school {
  font-family: 'Flexible W400';
  font-size: 2.25rem;

  /* color: var(--accent); */
  color: var(--bg-primary);
  
  margin-top: -4rem;
  /* text-shadow: 1px 1px rgb(52, 188, 238), -1px -1px rgb(51, 232, 75); */
  text-shadow: 1px 1px rgb(52, 188, 238), -2px -2px rgb(51, 232, 75);
}

.conent-panel {
  margin-left: auto;
  margin-right: 3rem;

  color: var(--accent);

  /* font-size: 3.5rem;
  font-family: 'Newake-Font'; */
  font-size: 3.5rem;
  font-family: 'Flexible W400';
  text-transform: uppercase;
  letter-spacing: 0.1rem;

  display: flex;
  align-items: stretch;

  /* text-shadow: 2px 2px rgb(90, 50, 40); */
  text-shadow: 1px 1px rgb(55, 120, 166);
}