  /* --bg: rgb(12, 12, 12); */
  /* --bg: #DBD6CC; */
  /* font-size: 16px; */
  /* font-size: 1vh; */
  font-size: min(1vh, 0.8vw);

  overflow-x: hidden;
}

#root {
  overflow-x: hidden;
}

