.resume-section {
  height: 10vh;
  width: 100vw;

  display: flex;
  justify-content: flex-end;
  align-content: flex-end;

  background-color: var(--bg-primary);
  /* background-color: var(--bg-complementary); */
}

.resume-container {
  /* margin-top: -25rem; */
  margin-top: 65rem;
  /* margin-right: 40rem; */
  margin-left: auto;
  /* padding-left: 100rem; */
  padding-bottom: 30rem;

  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-items: center;

  /* transition: margin-right 300ms ease, margin-top 300ms ease; */
}

.resume-preview {
  --size: 40rem;
  --aspect-ratio: 1.295;

  cursor: pointer;

  width: var(--size);
  height: calc(var(--size) * var(--aspect-ratio));

  
  background-color: rgb(41, 41, 41);
  background-image: url('assets/resume-preview.png');
  background-size: var(--size);
  background-repeat: no-repeat;
  
  filter: blur(1px);
  
  box-shadow: 0px 0px 0.3rem 0.075rem var(--bg-primary) inset;

  transition: filter 300ms ease, all 300ms ease;
}

.resume-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;

  padding-bottom: 5rem;
  padding-left: 4rem;
  padding-right: 8rem;

  font-family: 'Flexible W400';
  font-size: 13rem;
  line-height: 7rem;

  margin-top: -2rem;

  color: var(--accent);
  text-shadow: 1px 1px rgb(212, 128, 89), -1px -1px rgb(125, 231, 255);
  transition: color 300ms ease;
}

.resume-preview:hover {
  filter: blur(0px);
}






.pdf-container {
  z-index: 5;

  position: fixed;
  left: 0;
  right: 0;

  width: 100vw;
  height: 100vh;

  margin-top: -100vh;
  margin-left: auto;
  margin-right: auto;

  text-align: center;

  background-color: transparent;
  transition: background-color 300ms ease;
  transition-delay: 0ms;
}

.pdf-container-enter-active {
  background-color: rgba(5, 7, 10, 0.9);
  transition-delay:0ms;
}

.pdf-container-enter-done {
  background-color: rgba(5, 7, 10, 0.9);
}



.pdf-viewer {
  position: relative;
  left: 0;
  right: 0;
  z-index: 10;

  width: 70.5vh;
  height: 90vh;

  margin-top: 5vh;
  margin-left: auto;
  margin-right: auto;

  text-align: center;

  opacity: 0;
  transition: opacity 250ms ease;
  transition-delay: 0ms;

  /* overflow-x: hidden;
  overflow-y: hidden; */
}

.pdf-container-enter-active .pdf-viewer {
  opacity: 1.0;
  transition-delay: 500ms;
}

.pdf-container-enter-done .pdf-viewer {
  opacity: 1.0;
}


.pdf-help-text {
  width: fit-content;

  margin-left: auto;
  margin-right: auto;
  text-align: center;

  margin-top: 1rem;

  color: rgba(255, 255, 255, 0);
  font-size: 1.5rem;
  font-family: 'BigJhon', 'Courier New', Courier, monospace;

  transition: color 500ms ease;
}

.pdf-container-enter-active .pdf-help-text {
  color: white;
  /* transition-delay: 500ms; */
}

.pdf-container-enter-done .pdf-help-text {
  color: white;
}

.pdf-loading-text {
  width: fit-content;

  margin-left: auto;
  margin-right: auto;
  text-align: center;

  margin-top: -50vh;

  color: rgba(255, 255, 255, 0);
  font-size: 1.5rem;

  transition: none;
}

.pdf-container-enter-active .pdf-loading-text {
  color: white;
  transition: color 500ms ease;
}

.pdf-container-enter-done .pdf-loading-text {
  color: white;
  transition: color 500ms ease;
}



